<template>
  <background-transparent-view :backgroundModal="backgroundModal" :closeModal="closeModal"></background-transparent-view>
  <div v-if="isLoading" class="d-flex align-items-center" style="height: 100%">
    <div class="loading-spinner"></div>
  </div>
  <div v-else>
    <header-view :catologModalFunc="catologModalFunc"  :loginModal="loginModal" :loginModalFunc="loginModalFunc" :catologModal="catologModal" :selected_products="selected_products.length" :favourite_products="favourite_products_data.length"></header-view>
      <div class="carusel_modal" v-if="checkoutModal" style="height: 160%">
        <div class="d-flex align-items-start">
          <div class="confirm_modal">
            <div class="mb-4 text-start">
              <div class="text-center">
                <span>
                {{$t('Do you confirm this order?')}}
                </span>
              </div>
              <div>
                <span>{{$t('User name')}}: </span>
                <b>{{first_name}}</b>
              </div>
              <div>
                <span>{{$t('User phone')}}: </span>
                <b>{{phone_number}}</b>
              </div>
              <div>
                <span>{{$t('Price')}}: </span>
                <b>{{all_sum + 18000 - selected_coupon.coupon_price}} {{$t('Sum')}}</b>
              </div>
              <div v-if="Object.keys(address).length > 0">
                <span>{{$t('Address')}}: </span>
                <b>{{address.region.name+' '+address.city.name+' '+address.name }}</b>
              </div>
              <div>
                <span>{{$t('Payment method')}}: </span>
                <b>{{$t(payment)}}</b>
              </div>
            </div>
            <div class="d-flex justify-content-around">
              <button class="btn btn-danger" @click="closeModal">{{$t('No')}}</button>
              <button class="btn btn-success" @click="confirmOrder">{{$t('Yes')}}</button>
            </div>
          </div>
        </div>
      </div>

      <section class="mt-5">
        <div class="container d-flex">
          <ul class="d-flex" style="margin-left: -35px;">
            <li class="d-flex ms-1">
              <router-link to="/" style="text-decoration: none; color: #000000;">
                {{$t('Home')}}
              </router-link>
              <div class="ms-1">
                <img src="../assets/uploads/icons/arrow-left.svg" alt="arrow-left">
              </div>
            </li>
            <li class="d-flex ms-1">
              <router-link to="/profile" style="text-decoration: none; color: #000000;">
                {{$t('Personal Area')}}
              </router-link>
              <div class="ms-1">
                <img src="../assets/uploads/icons/arrow-left.svg" alt="arrow-left">
              </div>
            </li>
            <li class="d-flex ms-1">
              <router-link to="/basket" style="text-decoration: none; color: #000000;">
                {{$t('Basket')}}
              </router-link>
              <div class="ms-1">
                <img src="../assets/uploads/icons/arrow-left.svg" alt="arrow-left">
              </div>
            </li>
          </ul>
        </div>
      </section>

      <section>
        <div class="container d-flex">
          <h1 class="basket_big-text">{{$t('Basket')}}</h1>
          <h3 class="basket__product-count ms-3" style="margin-right: 3rem; margin-top: 1rem;">({{selected_products.length}} {{$t('goods')}})</h3>
          <div class="d-flex header_bottom_text" style="margin-left: 3rem;">
            <img style="width: 20px; height: 20px;" class="mt-3 send_and_trash-btn ms-3 me-2" src="../assets/uploads/icons/share.svg" alt="share">
            <h4 class="basket_small-text">{{$t('Share')}}</h4>
          </div>
          <div class="d-flex ms-3">
            <img style="width: 20px; height: 20px;" class="mt-3 send_and_trash-btn ms-3 me-2" src="../assets/uploads/icons/trash.svg" alt="trash">
            <h4 class="basket_small-text">{{$t('Empty trash')}}</h4>
          </div>
        </div>
      </section>

      <section>
        <div class="container d-flex basket_father">
          <div class="me-4">
            <div class="d-flex flex-column">
              <div v-if="selected_products.length == 0" class="basket_products mt-4 d-flex align-items-center justify-content-center">
                <span>{{$t('No products')}}</span>
              </div>
              <div v-for="(product, index) in selected_products" :key="index" class="basket_products mt-4">
                <router-link :to="'/show-detail/'+product.product_id" style="text-decoration: none;" class="d-flex align-items-center justify-content-around">
                  <div class="save_a_like-image_basket" :style="'background-image: url('+ product.images +')'"></div>
                  <div class="basket_products_content">
                    <p class="basket_title basket_title_padding"> <b>{{product.name}}</b></p>
                    <span class="basket_title" style="opacity: 0.5;">{{product.company}}</span>
                    <div class="product_color" :style="'background-color:'+product.color.code"></div>
                  </div>
                  <div style="height: 100%">
                    <div>
                      <p class="basket_title" style="opacity: 0.7;">{{product.sum}} {{$t('sum')}}</p>
                      <p v-if="parseInt(product.price) > parseInt(product.sum)" class="basket_title" style="opacity: 0.3;"><del>{{product.price}} {{$t('sum')}}</del></p>
                    </div>
                  </div>
                </router-link>
                <div style="flex-direction: column;">
                  <div class="d-flex">
                    <button class="basket_minus me-2" @click="minusFunc(index)"></button>
                    <input class="basket_number-input" type="text" :value="product.count" id="input"/>
                    <button class="basket_minus ms-2 me-3" @click="plusFunc(index)">+</button>
                    <h3 class="basket_price">{{product.sum*product.count}} {{$t('sum')}}</h3>
                  </div>
                  <div class="basket_trash-btn" style="margin-top: 20px; position: relative; left: 90px;">
                    <h3 class="basket_price-mobile">{{product.sum*product.count}} {{$t('sum')}}</h3>
                    <div class="d-flex">
                      <div style="cursor: pointer;" id="activeDiv" @click="toFavourite(product)" class="me-2">
                        <svg v-if="product.is_favourite == undefined || product.is_favourite == null || product.is_favourite != true" style="cursor: pointer;" width="20" height="20" id="activeSvg" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path id="activePath" d="M17.1582 4.14159C17.8915 4.96659 18.3332 6.04992 18.3332 7.24158C18.3332 13.0749 12.9332 16.5166 10.5165 17.3499C10.2332 17.4499 9.7665 17.4499 9.48317 17.3499C7.0665 16.5166 1.6665 13.0749 1.6665 7.24158C1.6665 4.66659 3.7415 2.58325 6.29984 2.58325C7.8165 2.58325 9.15817 3.31659 9.99984 4.44992C10.8415 3.31659 12.1915 2.58325 13.6998 2.58325" stroke="#8DC73F" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                        </svg>
                        <svg v-else style="cursor: pointer;" width="20" height="20" id="activeSvg" viewBox="0 0 20 20" fill="#8DC73F" xmlns="http://www.w3.org/2000/svg">
                          <path id="activePath" d="M17.1582 4.14159C17.8915 4.96659 18.3332 6.04992 18.3332 7.24158C18.3332 13.0749 12.9332 16.5166 10.5165 17.3499C10.2332 17.4499 9.7665 17.4499 9.48317 17.3499C7.0665 16.5166 1.6665 13.0749 1.6665 7.24158C1.6665 4.66659 3.7415 2.58325 6.29984 2.58325C7.8165 2.58325 9.15817 3.31659 9.99984 4.44992C10.8415 3.31659 12.1915 2.58325 13.6998 2.58325" stroke="#8DC73F" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                        </svg>
                      </div>
                      <button @click="deleteFunc(index)">
                        <img width="20" height="20" src="../assets/uploads/icons/trashRed.svg" alt="trash">
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="basket_total mt-4 all_product" style="padding: 25px;">
            <div class="d-flex justify-content-between mt-1">
              <h3 class="basket_total-all-price">{{ $t("Products price") }} <span style="opacity: 0.5">({{selected_products.length}})</span></h3>
              <h3 class="basket_total-all">{{all_price}} {{$t('Sum')}}</h3>
            </div>
            <div class="d-flex justify-content-between mt-1" v-if="selected_coupon.coupon.id != ''">
              <h3 class="basket_total-all">{{ $t("Coupon") }} "{{selected_coupon.coupon.name}}"</h3>
              <h3 class="basket_total-all">{{- selected_coupon.coupon_price}} {{$t('Sum')}}</h3>
            </div>
            <div class="d-flex justify-content-between mt-1" v-if="all_price - all_sum - selected_coupon.coupon_price > 0">
              <h3 class="basket_total-all">{{ $t("Discount") }}</h3>
              <h3 class="basket_total-all"> - {{all_price - all_sum - selected_coupon.coupon_price}} {{$t('Sum')}}</h3>
            </div>
            <div class="d-flex justify-content-between mt-1">
              <h3 class="basket_total-all-price">{{ $t("Products sum") }}<span style="opacity: 0.5">({{selected_products.length}})</span></h3>
              <h3 class="basket_total-all">{{all_sum}} {{$t('Sum')}}</h3>
            </div>
            <div class="d-flex justify-content-between mt-1">
              <h3 class="basket_total-all">{{ $t("Delivery") }}</h3>
              <h3 class="basket_total-all">18 000 {{$t('Sum')}}</h3>
            </div>
            <div class="d-flex justify-content-between mt-1">
              <h3 class="basket_total-all">{{ $t("Total amount") }}</h3>
              <h3 class="basket_total-all">{{all_sum + 18000 - selected_coupon.coupon_price}} {{$t('Sum')}}</h3>
            </div>
            <div class="mt-5" v-if="getUser">
              <div class="mt-1">
                <h3 class="basket_total-all"><b>{{ $t("User name") }}</b></h3>
                <div class="d-none" v-if="!first_name_change">
                  {{first_name = getUser.first_name}}
                </div>
                <input type="text" class="form-control" v-model="getUser.first_name" @change="getFirstName">
              </div>
             <div class="mt-1">
               <h3 class="basket_total-all"><b>{{ $t("User phone") }}</b></h3>
               <div class="d-none" v-if="!phone_number_change">
                 {{phone_number = getUser.phone_number}}
               </div>
               <input type="text" class="form-control" v-model="getUser.phone_number" @change="getPhoneNumber">
             </div>
            </div>
            <div class="mt-3">
              <h3 class="basket_total-all"><b>{{ $t("Delivery address") }}</b></h3>
<!--              <p class="basket_total-addres">{{ $t("Tashkent city, district. Chilonzar, 13, apt. 12v") }}</p>-->
              <div v-if="my_addresses.length>0">
                <div class="d-none" v-if="!address_change">
                  {{address = my_addresses[0]}}
                </div>
                <select v-model="address" class="form-select" @change="addressChange" @click="addressClick">
                  <option v-for="(my_address, index) in my_addresses" v-bind:key="index" :value="my_address">
                    <span class="basket_total-addres">{{my_address.region.name+' '+my_address.city.name+' '+my_address.name }}</span>
                  </option>
                </select>
              </div>
              <div class="d-flex justify-content-start">
                <router-link class="mt-2" to="/profile-address">
                  <button class="button_location-green" style="margin-right: 0px"><img src="../assets/uploads/icons/location-add.svg" alt="Location"></button>
                </router-link>
              </div>

            </div>
            <div class="mt-3 text-start">
              <label class="mb-2" v-if="selected_coupon.coupon.id != ''">{{$t('Enter another coupon')}}</label>
              <form class="d-flex">
                <input class="basket_coupon" type="text" v-model="coupon" :placeholder="$t('Enter coupon')">
                <button class="basket_coupon-btn" @click="takeCoupon">{{ $t("Apply") }}</button>
              </form>
            </div>
            <div class="mt-4">
              <h3 class="basket_total-all"><b>{{ $t("Payment method") }}</b></h3>
              <div class="d-flex mt-3">
                <div class="d-flex justify-content-center align-items-center">
                  <input type="radio" id="html" class="basket_pay me-2" name="fav_language" @change="selectPayment" checked value="Cash">
                  <label for="html">{{$t("Cash")}}</label><br>
                </div>
                <div class="d-flex justify-content-center align-items-center ms-4">
                  <input type="radio" id="css" class="basket_pay me-2" name="fav_language" @change="selectPayment" value="Online">
                  <label for="css">{{$t("Online")}}</label><br>
                </div>
              </div>
            </div>
            <div class="mt-4 d-flex" style="justify-content: space-around;">
              <img width="50" height="50" src="../assets/uploads/payment-image/Payme.svg" alt="Payme">
              <img width="50" height="50" src="../assets/uploads/payment-image/Click.svg" alt="Click">
              <img width="50" height="50" src="../assets/uploads/payment-image/Humo.svg" alt="Humo">
              <img width="50" height="50" src="../assets/uploads/payment-image/Uzcard.svg" alt="Uzcard">
              <img width="50" height="50" src="../assets/uploads/payment-image/Visa.svg" alt="Visa">
            </div>
            <div class="d-flex justify-content-center">
              <button class="mt-4 basket_order-btn" @click="checkoutOpen">{{$t('Go to checkout')}}</button>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div class="container detail_container mt-5" style="height: 464px">
          <h3 class="comment_title pt-4">{{$t('You may like')}}</h3>
          <swiper-product></swiper-product>
        </div>
      </section>
    <footer-view></footer-view>
  </div>
</template>
<script>
  import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
  import HeaderView from "@/components/HeaderView";
  import FooterView from "@/components/FooterView";
  import router from "@/router";
  import axios from "axios";
  export default {
    components: {FooterView, HeaderView},
    computed:{
      ...mapState(['backgroundModal', 'catologModal', 'loginModal', 'checkoutModal']),
      ...mapGetters(['getProducts', 'getFavourites', 'getUser']),
    },
    data(){
      return{
        name:'Elyor',
        selected_products:[],
        first_name:'',
        phone_number:'',
        coupon_products:[],
        selected_products_data:[],
        selected_coupon:{
          coupon:{
            id:'',
            name:''
          },
          coupon_price:0
        },
        payment:'Cash',
        my_addresses:[],
        address:{},
        all_sum:0,
        all_price:0,
        coupon_price:0,
        favourite_product:{},
        favourite_products:[],
        favourite_products_data:[],
        favourite_product_element:'no element',
        is_repeated:false,
        router_id:router.currentRoute.value.params.id,
        isLoading:true,
        coupon:'',
        address_change:false,
        first_name_change:false,
        phone_number_change:false,
        address_changed:false,
        // charaterized_products:[],
      }
    },
    methods:{
      ...mapMutations(['closeModal', 'catologModalFunc', 'toFavorites', 'getFavouritedProducts', 'loginModalFunc', 'checkoutOpen']),
      ...mapActions(['GETPRODUCTSFUNC']),
      plusFunc(index){
        this.selected_products[index].count++
        this.all_sum = 0
        this.all_price = 0
        this.allSumFunc()
      },
      selectPayment(e){
        this.payment = e.target.value
      },
      addressChange(){
        this.address_changed = true
      },
      addressClick(){
        if(!this.address_changed){
          this.address_change = true
        }
      },
      minusFunc(index){
        if(this.selected_products[index].count > 0){
          this.selected_products[index].count = this.selected_products[index].count - 1
        }
        this.all_sum = 0
        this.all_price = 0
        this.allSumFunc()
      },
      getAllSum(item){
        this.all_sum = this.all_sum + parseInt(item.sum)*parseInt(item.count)
        this.all_price = this.all_price + parseInt(item.price)*parseInt(item.count)
      },
      allSumFunc(){
        this.all_sum = 0
        this.all_price = 0
        this.selected_products.forEach(this.getAllSum)
      },
      getFirstName(e){
        this.first_name = e.target.value
        this.first_name_change = true
      },
      getPhoneNumber(e){
        this.phone_number = e.target.value
        this.phone_change_change = true
      },
      async confirmOrder(){
        try{
          const res = await axios.post(process.env.VUE_APP_SERVER+'/confirm-order',
              {
                'selected_products':this.selected_products,
                "coupon":this.selected_coupon.coupon.name,
                "payment":this.payment,
                "address_id":this.address.id,
                "first_name":this.first_name,
                "phone_number":this.phone_number
              }, {
                headers:{
                  'Content-type':'multipart/form-data',
                  'Authorization':`Bearer ${localStorage.getItem('user_token')}`
                }
              })
          console.log(res)
          if(res.data.status == true){
            this.$toast.success(this.$t('Order confirmed !'));
            if(localStorage.getItem('selected_product')){
              localStorage.removeItem('selected_product')
            }
            if(localStorage.getItem('coupon')){
              localStorage.removeItem('coupon')
            }
            location.reload();
          }else{
            this.$toast.warning(this.$t('Order not confirmed !'));
          }
        }catch (e) {
          console.log(e)
        }
      },
      deleteFunc(index){
        this.selected_products.splice(index, 1)
        if(localStorage.getItem('selected_product')){
          localStorage.removeItem('selected_product')
        }
        localStorage.setItem('selected_product', JSON.stringify(this.selected_products))
        this.allSumFunc()
        this.$toast.warning(this.$t('Product deleted from basket !'));
      },
      async takeFavouriteProducts(){
        // this.all_sum = 0
        let selected_products_id_ = []
        if(localStorage.getItem('favourite_product') && localStorage.getItem('selected_product')){
          this.favourite_products_data = JSON.parse(localStorage.getItem('favourite_product'))
          selected_products_id_ = JSON.parse(localStorage.getItem('selected_product')).map(item => item.id)
        }
        try {
          this.favourite_products = await this.getFavouriteProduct(selected_products_id_)
        }catch (e) {
          console.log(e)
        }
        // for(let i=0; i<this.favourite_products.length; i++){
        //   for(let j=0; j<this.favourite_products_data.length; j++) {
        //     if (this.favourite_products_data[j].id == this.favourite_products[i].product_id) {
        //       this.favourite_products[i].count = this.favourite_products_data[j].count;
        //       // this.all_sum = this.all_sum + this.favourite_products[i].sum*this.favourite_products[i].count
        //     }
        //   }
        // }
      },
      async SelectedProducts(){
        this.all_sum = 0
        this.all_price = 0
        let selected_products = []
        this.selected_products_data = []
        if(localStorage.getItem('selected_product')){
          this.selected_products_data = JSON.parse(localStorage.getItem('selected_product'))
          for(let i=0; i<this.selected_products_data.length; i++){
            selected_products.push({
              'id':this.selected_products_data[i].id,
              'count':this.selected_products_data[i].count
            })
          }
        }

        // if(localStorage.getItem('selected_product')){
        //   this.selected_products_data = JSON.parse(localStorage.getItem('selected_product'))
        //   selected_products_id = JSON.parse(localStorage.getItem('selected_product')).map(item => item.id)
        // }
        if(localStorage.getItem('coupon')){
          this.selected_coupon = JSON.parse(localStorage.getItem('coupon'))
        }
        try {
            this.selected_products = await this.getCharacterizedProduct(selected_products, this.selected_coupon.coupon.name)
        }catch (e) {
          console.log(e)
          this.selected_products = []
        }
        if(this.selected_products != undefined && this.selected_products != null) {
          for (let i = 0; i < this.selected_products.length; i++) {
            for (let j = 0; j < this.selected_products_data.length; j++) {
              if (this.selected_products_data[j].id == this.selected_products[i].id) {
                this.selected_products[i].count = this.selected_products_data[j].count;
                this.all_sum = this.all_sum + parseInt(this.selected_products[i].sum) * parseInt(this.selected_products[i].count)
                this.all_price = this.all_price + parseInt(this.selected_products[i].price) * parseInt(this.selected_products[i].count)
              }
            }
          }
        }
        this.getSelectedProduct()
      },
      async getFavouriteProduct(selected_products_id){
        try{
          const res = await axios.post(process.env.VUE_APP_SERVER+'/get-favourite-products',
              {'selected_products_id':selected_products_id})
          if(res.data.status == true){
            return res.data.data
          }else{
            return []
          }
        }catch (e) {
          console.log(e)
          return []
        }
      },
      async getCharacterizedProduct(selected_products, coupon_name){
        try{
          const res = await axios.post(process.env.VUE_APP_SERVER+'/get-characterized-product',
          {
            'selected_products':selected_products,
            "coupon":coupon_name,
          }, {
            headers:{
              'Content-type':'multipart/form-data',
              'Authorization':`Bearer ${localStorage.getItem('user_token')}`
            }
          })
          if(res.data.status == true){
            this.selected_coupon.coupon_price = res.data.coupon_price
            return res.data.data
          }else{
            return []
          }
        }catch (e) {
          console.log(e)
          return []
        }
      },
      getSelectedProduct(){
        this.takeFavouriteProducts()
        for(let j=0; j<this.favourite_products_data.length; j++){
          if(this.selected_products != undefined && this.selected_products != null){
            for(let i=0; i<this.selected_products.length; i++){
              if(this.selected_products[i].product_id == this.favourite_products_data[j].id){
                Object.assign(this.selected_products[i], {'is_favourite':true})
              }
            }
          }
        }
      },
      async getCoupon(products){
        try{
          const res = await axios.post(process.env.VUE_APP_SERVER+'/get-coupon',
            {
              "coupon":this.coupon,
              "products":products,
            }, {
            headers:{
              'Content-type':'multipart/form-data',
              'Authorization':`Bearer ${localStorage.getItem('user_token')}`
            }
          })
          if(res.data.status == true){
            this.coupon_price = res.data.data.coupon_price
            if(localStorage.getItem('coupon')){
              localStorage.removeItem('coupon')
            }
            if(res.data.data.coupon.id != null && res.data.data.coupon.id != undefined){
              localStorage.setItem('coupon', JSON.stringify(res.data.data))
            }
            location.reload()
          }
        } catch (e) {
          console.log(e)
        }
      },
      async takeCoupon(e){
        e.preventDefault()
        let selected_products = []
        let products = []
        if(localStorage.getItem('selected_product')){
          selected_products = JSON.parse(localStorage.getItem('selected_product'))
          for(let i=0; i<selected_products.length; i++){
            products.push({
              'id':selected_products[i].id,
              'count':selected_products[i].count
            })
          }
        }
        try {
          this.coupon_products = await this.getCoupon(products)
        }catch (e) {
          console.log(e)
        }
      },
      toFavourite(product) {
        this.takeFavouriteProducts()
        this.is_repeated = false
        this.favourite_product.id = product.product_id
        for(let i = 0; i<this.favourite_products_data.length; i++){
          if(this.favourite_products_data[i].id == product.product_id) {
            this.is_repeated = true
            this.favourite_product_element = i
            for(let j = 0; j<this.selected_products.length; j++){
              if(this.selected_products[j].product_id == product.product_id){
                if(this.selected_products[j].is_favourite != undefined && this.selected_products[j].is_favourite != null){
                  delete this.selected_products[j].is_favourite
                }
              }
            }
          }
        }
        if(this.favourite_product_element != 'no element' && this.is_repeated == true){
          this.favourite_products_data.splice(parseInt(this.favourite_product_element), 1)
        }
        this.favourite_product_element = 'no element'
        if(this.is_repeated == false){
          for(let j = 0; j<this.selected_products.length; j++){
            if(this.selected_products[j].product_id == product.product_id){
              if(this.selected_products[j].is_favourite == undefined || this.selected_products[j].is_favourite == null){
                Object.assign(this.selected_products[j], {'is_favourite':true})
              }
            }
          }
          if(this.favourite_products_data.length == 0){
            this.favourite_products_data = [{'id':this.favourite_product.id}]
          }else {
            this.favourite_products_data.push({'id':this.favourite_product.id})
          }
        }
        if(localStorage.getItem('favourite_product')){
          localStorage.removeItem('favourite_product')
        }
        localStorage.setItem('favourite_product', JSON.stringify(this.favourite_products_data))
        if(this.is_repeated == false){
          this.$toast.success(this.$t('Product added to favourites !'));
        }else{
          this.$toast.warning(this.$t('Product deleted from favourites !'));
        }
      },
      async getAddress(){
        try{
          const addresses = await axios.get(process.env.VUE_APP_SERVER+'/get-address', {
            headers:{
              'Content-type':'Application/json',
              'Authorization':`Bearer ${localStorage.getItem('user_token')}`
            }
          })
          this.my_addresses = addresses.data.data
        }catch (e) {
          console.log(e)
        }
      }
    },
    mounted() {
      this.getAddress()
      this.GETPRODUCTSFUNC()
      this.SelectedProducts()
      this.getFavouritedProducts()
      this.allSumFunc()
      setTimeout(() => {
        this.isLoading = false; // Set to false when the loading is complete
      }, 400);
    }
  }
</script>
