<template>
  <div class="seen_product-card mt-4">
    <div class="mt-3 margin-profile d-flex justify-content-between">
      <h3 class="hello_profile">{{$t('Last views')}}</h3>
      <button class="delete_history" @click="historyDelete">{{$t('Clear the history')}} <img style="margin-top: -5px;" src="../assets/uploads/icons/trashWhite.svg" alt=""></button>
    </div>
    <div class="seen_product_modal row">
      <div class="col-lg-4 col-md-6 col-sm-12 d-flex justify-content-center" v-for="(product, index) in getProducts" :key="index"
      style="height: 394px">
        <router-link :to="'/show-detail/'+product.id" id="card_like" style="width: 100%;">
          <product-view :basket="product" @mouseenter="showBasket(index)"  @mouseleave="fromBasket(index)"></product-view>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name:'last-seen-view',
  computed:{
    ...mapGetters(["basket", 'getProducts']),
  },
  props:{
    current_menu:{
      type:Object,
      required:true
    },
    historyDelete:{
      type:Function,
    }
  },
  data(){
    return{
     name:"Elyor"
    }
  },
  methods:{
    // ...mapMutations(["historyDelete"]),
    ...mapActions(['GETPRODUCTSFUNC']),
    showBasket(i){
      if(this.getProducts[i].basket_button == false){
        this.getProducts[i].basket_button = true;
      }
    },
    fromBasket(i){
      if(this.getProducts[i].basket_button == true){
        this.getProducts[i].basket_button = false;
      }
    },
  },
  mounted() {
    this.GETPRODUCTSFUNC()
  }
}
</script>
<style>
.seen_product_modal{
  flex-wrap: wrap;
  height: 800px;
  overflow-y: scroll;
  display: flex;
  justify-content: space-around;
}
</style>